export default {
  props: {
    actions: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      offset: 105,
      lists: [],
      pupoList: []
    }
  },
  mounted () {
    this.SplitAction()
  },
  beforeDestroy () {
    clearTimeout(this.timer)
  },
  methods: {
    // 点击选中
    select (item) {
      this.offset = 105
      if (this.timer) {
        clearTimeout(this.timer)
      }

      this.timer = setTimeout(() => {
        this.$emit('selectActionMenu', item)
      }, 400)
    },
    // 分类 哪里在底部显示，哪里在pupo中显示
    SplitAction () {
      let lists = []
      let pupoList = []

      if (this.actions.length > 2) {
        this.actions.forEach((item, index) => {
          if (index < 2) {
            lists.push(item)
          } else {
            pupoList.push(item)
          }
        })
      } else {
        lists = [...this.actions]
      }

      this.lists = [...lists]
      this.pupoList = [...pupoList]
    },
    // 控制显示和隐藏
    toggle () {
      this.offset = this.offset === 105 ? 0 : 105
    }
  },
  watch: {
    actions (newActions) {
      this.SplitAction()
    }
  }
}



